
/**
 *  –ë–∞–∑–æ–≤—ã–π –Ω–∞–±–æ—Ä —Ñ—É–Ω–∫—Ü–∏–π –¥–ª—è –ø—Ä–æ–µ–∫—Ç–∞
 * */
export default class Support {

  /**
   * –£–¥–∞–ª–µ–Ω–∏—è –∫–ª–∞—Å—Å(–æ–≤) —É —ç–ª–µ–º–µ–Ω—Ç–æ–≤
   * @param {string} elementSelector - —Å–µ–ª–µ–∫—Ç–æ—Ä —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —É –∫–æ—Ç–æ—Ä—ã—Ö –Ω–∞–¥–æ —É–¥–∞–ª–∏—Ç—å –∫–ª–∞—Å—Å(—ã).
   * @param {array} arrayClasses     - –º–∞—Å—Å–∏–≤ —Å–æ —Å–ø–∏—Å–∫–æ–º –∫–ª–∞—Å—Å–æ–≤.
   * @return {void}
   */
  static removeClass(elementSelector,arrayClasses) {
    document.querySelectorAll(elementSelector).forEach(element => {
      element.classList.remove(...arrayClasses)
    })
  }

  /**
   * –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–ª–∞—Å—Å(–æ–≤) —É –±–ª–∏–∂–∞–π—â–µ–≥–æ –ø–æ–¥—Ö–æ–¥—è—â–µ–≥–æ —Ä–æ–¥–∏—Ç–µ–ª—è
   * @param {Element} element       - —ç–ª–µ–º–µ–Ω—Ç —É –∫–æ—Ç–æ—Ä–æ–≥–æ –Ω–∞–¥–æ –Ω–∞–π—Ç–∏ –±–ª–∏–∂–∞–π—â–µ–≥–æ –ø–æ–¥—Ö–æ–¥—è—â–µ–≥–æ —Ä–æ–¥–∏—Ç–µ–ª—è.
   * @param {string} searchSelector - —Å–µ–ª–µ–∫—Ç–æ—Ä –∫–æ—Ç–æ—Ä—ã–π –Ω–∞–¥–æ –Ω–∞–π—Ç–∏ —É —Ä–æ–¥–∏—Ç–µ–ª—è.
   * @param {array} arrayClasses    - –º–∞—Å—Å–∏–≤ —Å–æ —Å–ø–∏—Å–∫–æ–º –∫–ª–∞—Å—Å–æ–≤.
   * @return {void}
   */
  static addClosestClass(element,searchSelector ,arrayClasses) {
    element.closest(searchSelector).classList.add(...arrayClasses)
  }

  /**
   * –£–¥–∞–ª–µ–Ω–∏—è –∫–ª–∞—Å—Å(–æ–≤) —É –±–ª–∏–∂–∞–π—â–µ–≥–æ –ø–æ–¥—Ö–æ–¥—è—â–µ–≥–æ —Ä–æ–¥–∏—Ç–µ–ª—è
   * @param {Element} element       - —ç–ª–µ–º–µ–Ω—Ç —É –∫–æ—Ç–æ—Ä–æ–≥–æ –Ω–∞–¥–æ –Ω–∞–π—Ç–∏ –±–ª–∏–∂–∞–π—â–µ–≥–æ –ø–æ–¥—Ö–æ–¥—è—â–µ–≥–æ —Ä–æ–¥–∏—Ç–µ–ª—è.
   * @param {string} searchSelector - —Å–µ–ª–µ–∫—Ç–æ—Ä –∫–æ—Ç–æ—Ä—ã–π –Ω–∞–¥–æ –Ω–∞–π—Ç–∏ —É —Ä–æ–¥–∏—Ç–µ–ª—è.
   * @param {array} arrayClasses    - –º–∞—Å—Å–∏–≤ —Å–æ —Å–ø–∏—Å–∫–æ–º –∫–ª–∞—Å—Å–æ–≤.
   * @return {void}
   */
  static removeClosestClass(element,searchSelector ,arrayClasses) {
    element.closest(searchSelector).classList.remove(...arrayClasses)
  }

}
