
/**
 *  –ë–∞–∑–æ–≤—ã–π –∫–ª–∞—Å—Å –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å —Ñ–æ—Ä–º–∞–º–∏
 * */
export default class Form {

  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
   * @param {Element} form      - —Ñ–æ—Ä–º–∞.
   * @param {Object=} controls  - –ø–æ–ª—è —Ñ–æ—Ä–º—ã.
   */
  constructor(form, controls= {}) {
    this.form = form

    this.controls = controls
  }

  /**
   * –í—ã—Ç–∞—Å–∫–∏–≤–∞–µ—Ç –∑–Ω–∞—á–µ–Ω–∏—è –∏–∑ –ø–æ–ª–µ–π —Ñ–æ—Ä–º—ã
   * @return {Object}
   */
  value() {
    const value = {}

    Object.keys(this.controls).forEach(control => {
      value[control] = this.form[control].value
    })

    return value
  }

  /**
   * –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –≤–∞–ª–∏–¥–∞—Ü–∏—é
   * @return {boolean}
   */
  isValid() {

    let isFormValid = true // –§–ª–∞–≥

    Object.keys(this.controls).forEach(control => {

      if(this.controls[control].length) {

        const validators = this.controls[control] // –º–∞—Å—Å–∏–≤ —Å –≤–∞–ª–∏–¥–∞—Ç–æ—Ä–∞–º–∏

        let isValid = true // –§–ª–∞–≥

        validators.forEach(validator =>{

          isValid = validator(this.form[control].value) && isValid // –∑–∞–ø—É—Å–∫–∞–µ–º –≤–∞–ª–∏–¥–∞—Ç–æ—Ä—ã –ø–æ —Ü–µ–ø–æ—á–∫–∏

        })

        //–µ—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã –≤–∞–ª–∏–¥–µ–Ω
        isValid ? clearError(this.form[control]) : setError(this.form[control])
        //–µ—Å–ª–∏ —ç–ª–µ–º–µ–Ω—Ç —Ñ–æ—Ä–º—ã –Ω–µ–≤–∞–ª–∏–¥–µ–Ω

        isFormValid = isFormValid && isValid // –ø–µ—Ä–µ–∫–ª—é—á–∞–µ–º –§–ª–∞–≥

      }

    })

    return isFormValid
  }

  /**
   * –û—á–∏—â–∞–µ–º —Ñ–æ—Ä–º—É
   * @return {void}
   */
  clear() {
    this.form.reset()
  }

}

/**
 * –°—Ñ–æ—Ä–º–∏—Ä–æ–≤–∞—Ç—å –∏ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –æ—à–∏–±–∫—É
 * @return {void}
 */
function setError($control){

  clearError($control) // —É–¥–∞–ª—è–µ—Ç —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–∏

  const error = '<span class="form__validation-error">–í–≤–µ–¥–∏—Ç–µ –∑–Ω–∞—á–µ–Ω–∏–µ</span>' // —Ñ–æ—Ä–º–∏—Ä—É–µ–º —Å–æ–æ–±—à–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–∏

  $control.nextElementSibling.firstElementChild.style.backgroundColor = '#fff5f5' // –ø–æ–¥—Å–≤–µ—Ç–∏—Ç—å –Ω–µ –≤–∞–ª–∏–¥–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç –∫—Ä–∞—Å–Ω—ã–º —Ü–≤–µ—Ç–æ–º

  $control.previousElementSibling.insertAdjacentHTML('beforeend', error) // –¥–æ–±–∞–≤–ª—è–µ–º —Å–æ–æ–±—â–µ–Ω–∏—è –æ—Ç –æ—à–∏–±–∫–∏ –¥–ª—è –Ω–µ–≤–∞–ª–∏–¥–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
}

/**
 * –£–¥–∞–ª–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–∏
 * @return {void}
 */
function clearError($control){

  $control.nextElementSibling.firstElementChild.style.backgroundColor = '' // —É–¥–∞–ª–∏—Ç—å –ø–æ–¥—Å–≤–µ—Ç–∫—É

  // —ç–ª–µ–º–µ–Ω—Ç —Å –æ—à–∏–±–∫–æ–π —Å—É—à–µ—Å—Ç–≤—É–µ—Ç
  if($control.previousElementSibling.querySelector('.form__validation-error')){

    $control.previousElementSibling.querySelector('.form__validation-error').remove() // —É–¥–∞–ª—è–µ—Ç –æ—à–∏–±–∫—É

  }

}
