import ModalCore from "../modalCore";

/**
 *  –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç
 * */
export default class Modal extends ModalCore{

  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
   * @param {string} triggerSelector - —Å–µ–ª–µ–∫—Ç–æ—Ä –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ.
   * @param {Object=} options        - –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—è.
   */
  constructor(triggerSelector ,
              {
                modalSelector = null,          // - —Å–µ–ª–µ–∫—Ç–æ—Ä –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –∫–æ—Ç–æ—Ä–æ–µ –º—ã –±—É–¥–µ–º –æ—Ç–∫—Ä—ã–≤–∞—Ç—å.
                closeSelector = '[data-sumbiot-modal-close]', // - —Å–µ–ª–µ–∫—Ç–æ—Ä –∫–æ—Ç–æ—Ä—ã–π –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ.
                closeClickOverlay = true,      // - –±—É–¥–µ—Ç –ª–∏ –∑–∞–∫—Ä—ã–≤–∞—Ç—å—Å—è –æ–∫–Ω–æ –ø–æ –∫–ª–∏–∫—É –ø–æ –ø–æ–¥–ª–æ–∂–∫–∏
              } = {}) {

    super()

    this._trigger = triggerSelector
    this.modal = document.querySelector(modalSelector || document.querySelector(triggerSelector).dataset.sumbiotTarget)
    this._close = this.modal.querySelector(closeSelector)

    this._windows = document.querySelectorAll('[data-sumbiot-modal]')
    this._closeClickOverlay = closeClickOverlay

    this._init()
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–æ–Ω–∞
   * @return {void}
   */
  _init() {
    this.hideAllModals()

    this._showHandler()

    this._closeHandler()
  }

  /**
   * –°–∫—Ä—ã–≤–∞–µ—Ç –≤—Å–µ –º–æ–¥–∞–ª—å–Ω—ã–µ –æ–∫–Ω–∞
   * @return {void}
   */
  hideAllModals() {
    this._windows.forEach(modal => {
      modal.classList.add('animated', 'fadeIn');
      modal.style.display = 'none';
    })
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –∫–ª–∏–∫–∞ –ø–æ —ç–ª–µ–º–µ–Ω—Ç—É –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
   * @return {void}
   */
  _showHandler() {
    document.addEventListener('click', (e) => {
      const target = e.target;

      if (target && target.classList.contains(this._trigger.slice(1)) || target.parentElement.classList.contains(this._trigger.slice(1)) ) {
        e.preventDefault()
        e.stopPropagation()

        this._show()

        setTimeout(() =>{
          target.blur()
        },150)
      }
    })
  }

  /**
   * –ü–æ–∫–∞–∑–∞—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
   * @return {void}
   */
  _show(){
    this.hideAllModals()

    this.modal.style.display = "block";
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –∫–ª–∏–∫–∞ –ø–æ —ç–ª–µ–º–µ–Ω—Ç—É –∫–æ—Ç–æ—Ä—ã–π –∑–∞–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
   * @return {void}
   */
  _closeHandler() {
    this._close.addEventListener('click', (e) => {
      if (e.target) {
        e.preventDefault()

        this._closeModal()
      }
    })

    this.modal.addEventListener('click', (e) => {
      if (e.target) {
        e.stopPropagation()

        this._closeModalOverlay(e)
      }
    })
  }

  /**
   * –°–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
   * @return {void}
   */
  _closeModal() {
    this.modal.style.display = "none";
  }

  /**
   * –°–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–µ –æ–∫–Ω–æ –ø–æ –∫–ª–∏–∫—É –Ω–∞ –ø–æ–¥–ª–æ–∂–∫—É
   * @return {void}
   */
  _closeModalOverlay(e) {

    if (e.target === this.modal && this._closeClickOverlay) {
      this.modal.style.display = "none";
    }
  }
}
