
/**
 *  –ü–æ–≥—Ä—É–∑—á–∏–∫
 * */
export default class Loader{

  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
   * @param {Object=} message - –æ–±—ä–µ–∫—Ç —Å –Ω–∞—Å—Ç—Ä–æ–π–∫–∞–º–∏
   */
  constructor({
                loading = '–ó–∞–≥—Ä—É–∑–∫–∞...',
                success = '–£—Å–ø–µ—Ö',
                failure = '–ù–µ—É–¥–∞—á–∞'
              } = {}) {

    this.message = {
      loading: {
        title: loading,
        img: ''
      },
      success: {
        title: success,
        img: 'loader__img--ok'
      },
      failure: {
        title: failure,
        img: 'loader__img--error'
      }
    }

    this._init()
  }

  /**
   * –ò–Ω—Ç–µ—Ä—Ñ–µ–π—Å –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
   * @return {void}
   */
  _init(){
    this.$el = document.createElement('div')
    this.$el.classList.add('loader')

    this.$el.innerHTML = `
      <div class="loader__img"></div>
      <p class="loader__massage">${this.message.loading.title}</p>
    `

    this.$img = this.$el.querySelector('.loader__img')
    this.$massage = this.$el.querySelector('.loader__massage')
  }

  /**
   * –ó–∞–≥—Ä—É–∑–∫–∞
   * @return {Element}
   */
  loading() {
    return this.$el
  }

  /**
   * –£—Å–ø–µ—Ö
   * @return {void}
   */
  success() {
    this.$img.classList.add(this.message.success.img)
    this.$massage.innerHTML = this.message.success.title
  }

  /**
   * –ù–µ—É–¥–∞—á–∞
   * @return {void}
   */
  failure() {
    this.$img.classList.add(this.message.failure.img)
    this.$massage.innerHTML = this.message.failure.title
  }

  /**
   * –£–¥–∞–ª–∏—Ç—å –∑–∞–≥—Ä—É–∑—á–∏–∫
   * @return {void}
   */
  removeLoader() {
    this.$el.remove()
  }
}
