import Modal from "./modal";

/**
 *  –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–∞ c –ø—Ä–∏–≤—è–∑–∫–æ–π –∫ –±–ª–æ–∫—É
 * */
export default class ModalDynamics extends Modal{

  /**
   * –°–æ–∑–¥–∞–µ—Ç —Å–∫—Ä—ã—Ç—ã–π input
   * @return {Node}
   */
  static createInput() {
    const input = document.createElement('input')

    input.classList.add('sumbiot-input-dynamic')

    input.setAttribute('type','hidden')
    input.setAttribute('name','ID')

    return input
  }

  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
   * @param {string} triggerSelector - —Å–µ–ª–µ–∫—Ç–æ—Ä –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ.
   * @param {Object=} options        - –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—è.
   */
  constructor(triggerSelector, options = {}) {

    super(triggerSelector,options)

    this._wrapperModalSelector = options.modalWrapper || '.sumbiot-wrapper'            // - —Ä–æ–¥–∏—Ç–µ–ª—å –∫—É–¥–∞ –≤—Å—Ç–∞–≤–ª—è–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    this._wrapperElementSelector = options.elementWrapper || '[data-sumbiot-wrapper]'  // - —Ä–æ–¥–∏—Ç–µ–ª—å –∫—É–¥–∞ –≤—Å—Ç–∞–≤–∏—Ç—å input
    this._existsElementForPasteIdSelector = options.existsElementForPasteId            // - —ç–ª–µ–º–µ–Ω—Ç —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç –≤ —Ñ–æ—Ä–º–µ –∏ –≥–æ—Ç–æ–≤ —Å –¥–æ–±–∞–≤–ª–µ–Ω–∏—é id
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –∫–ª–∏–∫–∞ –ø–æ —ç–ª–µ–º–µ–Ω—Ç—É –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
   * @return {void}
   */
  _showHandler() {
    document.addEventListener('click', (e) => {
      const target = e.target;

      if (target && target.classList.contains(this._trigger.slice(1))) {
          e.preventDefault()

          this._triggerEvent = target
          this._show()
      }else if(target.parentElement.classList.contains(this._trigger.slice(1))) {
          e.preventDefault()

          this._triggerEvent = target.parentElement
          this._show()
      }

    },true)
  }

  /**
   * –ü–æ–∫–∞–∑–∞—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
   * @return {void}
   */
  _show(){

    this.hideAllModals()

    this._modalPosition()

    this.modal.style.display = "block";
  }

  /**
   * –ü–æ–∑–∏—Ü–∏–∞–Ω–∏—Ä—É–µ—Ç –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –≤ –Ω—É–∂–Ω–æ–µ –º–µ—Å—Ç–æ
   * @return {void}
   */
  _modalPosition(){

    this._elementPosition()

    if(!this._triggerEvent.closest(this._wrapperModalSelector)) {
      this._triggerEvent.parentElement.classList.add(this._wrapperModalSelector.slice(1))
    }

    this._wpapper = this._triggerEvent.closest(this._wrapperModalSelector)
    this._wpapper.append(this.modal)
  }

  /**
   * –í—Å—Ç–∞–≤–ª—è–µ—Ç input –∏–ª–∏ –¥–æ–±–∞–≤–ª—è–µ—Ç data-sumbiot-id –≤ –Ω—É–∂–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç
   * @return {void}
   */
  _elementPosition() {
    let id = this._triggerEvent.dataset.id,
        elementForPasteId  = this.modal.querySelector(this._existsElementForPasteIdSelector || '.sumbiot-input-dynamic')

    if(!elementForPasteId) {
      elementForPasteId = ModalDynamics.createInput()

      this.modal.querySelector(this._wrapperElementSelector) ?
        this.modal.querySelector(this._wrapperElementSelector).prepend(elementForPasteId) :
        this.modal.querySelector('form').prepend(elementForPasteId)
    }

    this._existsElementForPasteIdSelector ? elementForPasteId.setAttribute('data-sumbiot-id',id) : elementForPasteId.setAttribute('value',id)
  }

}
