/**
 *  –ü–∞–Ω–µ–ª—å –¥–ª—è –≤—ã–ø–∞–¥–∞—é—â–µ–≥–æ —Å–ø–∏—Å–∫–∞, –¥–ª—è —Ä–∞—Å–∫—Ä—ã—Ç–∏—è —Å–ø–∏—Å–∫–∞ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏
 * */
export default class Stretch {

  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
   * @param {string} panelsSelector   - —Å–µ–ª–µ–∫—Ç–æ—Ä –ø–∞–Ω–µ–ª–µ–π –¥–ª—è –≤—ã–ø–∞–¥–∞—é—â–µ–≥–æ —Å–ø–∏—Å–∫–∞.
   * @param {string} stretchSelector  - —Å–µ–ª–µ–∫—Ç–æ—Ä —ç–ª–µ–º–µ–Ω—Ç–∞ –Ω–∞–¥ –∫–æ—Ç–æ—Ä—ã–º –±—É–¥–µ—Ç –ø—Ä–æ–≤–æ–¥–∏—Ç—Å—è –º–æ–¥–∏—Ñ–∏–∫–∞—Ü–∏—è.
   * @param {string} activeClass      - –∫–ª–∞—Å—Å –º–æ–¥–∏—Ñ–∏–∫–∞—Ç–æ—Ä –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞ –Ω–∞–¥ –∫–æ—Ç–æ—Ä—ã–º –±—É–¥–µ—Ç –ø—Ä–æ–≤–æ–¥–∏—Ç—Å—è –º–æ–¥–∏—Ñ–∏–∫–∞—Ü–∏—è.
   */
  constructor(panelsSelector,stretchSelector,activeClass) {

    this._panelsElements = document.querySelectorAll(panelsSelector)
    this._stretchSelector = stretchSelector
    this._activeClass = activeClass

    this._init()
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è
   * @return {void}
   */
  _init(){
    this._stretchHandler()
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –∫–ª–∏–∫–∞ –ø–æ –ø–∞–Ω–µ–ª–∏
   * @return {void}
   */
  _stretchHandler(){

    this._panelsElements.forEach(panel => {
      let stretchBtn = panel.querySelector('.option-panel__item--stretch')

      stretchBtn.addEventListener('click', (e) => {
        e.preventDefault()

        this._toggle(panel,stretchBtn)

      })

    })

  }

  /**
   * –†–∞–∑–≤–µ—Ä–Ω—É—Ç—å –∏–ª–∏ —Å–≤–µ—Ä–Ω—É—Ç—å —Å–ø–∏—Å–æ–∫ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏
   * @return {void}
   */
  _toggle(panelElement,stretchBtnElement){

    panelElement.closest(this._stretchSelector).classList.toggle(this._activeClass)

    stretchBtnElement.classList.toggle('option-panel__item--stretch-off')
    stretchBtnElement.title.toLowerCase() === '—Ä–∞–∑–≤–µ—Ä–Ω—É—Ç—å' ?
      stretchBtnElement.setAttribute('title','–°–≤–µ—Ä–Ω—É—Ç—å') :
      stretchBtnElement.setAttribute('title','–†–∞–∑–≤–µ—Ä–Ω—É—Ç—å')
  }

}
