
/**
 *  –í—ã–ª–∏–¥–∞—Ç–æ—Ä—ã –¥–ª—è —Ñ–æ—Ä–º
 * */
export class Validators {

  /**
   * –í–∞–ª–∏–¥–∞—Ç–æ—Ä: –≤—ã –Ω–µ–∑–∞–ø–æ–ª–Ω–∏–ª–∏ –ø–æ–ª–µ
   * @return {string}
   */
  static required(value = '') {
    return value && value.trim()
  }

  /**
   * –í–∞–ª–∏–¥–∞—Ç–æ—Ä: –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–µ —á–∏—Å–ª–æ —Å–∏–º–∞–æ–ª–æ–≤
   * @return {function}
   */
  static minLength(length) {
    return value => {
      return value.length >= length
    }
  }
}
