import DropdownCore from "../dropdownCore";

/**
 *  –í—ã–ø–æ–¥–∞—é—â–∏–π —Å–ø–∏—Å–æ–∫ —Å—Ç–∞–Ω–¥–∞—Ä—Ç
 * */
export default class Dropdown extends DropdownCore {

  /**
   * –ö–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
   * @param {string} dropdownSelector - —Å–µ–ª–µ–∫—Ç–æ—Ä –≤—ã–ø–æ–¥–∞—é—â–µ–≥–æ —Å–ø–∏—Å–∫–∞.
   * @param {Object=} options         - –∫–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—è.
   */
  constructor(dropdownSelector ,
              {
                dropdownToggleSelector = '.dropdown-sumbiot__toggle',        // - –∞–∫—Ç–∏–≤–Ω—ã–π –ø—É–Ω–∫—Ç
                dropdownOptionsWrapperSelector = '.dropdown-sumbiot__options' // - –≤—ã–ø–∞–¥–∞—é—â–∏–π —Å–ø–∏—Å–æ–∫
              } = {}) {

    super()

    this._dropdownSelector = dropdownSelector
    this._dropdownToggleSelector = dropdownToggleSelector
    this._listDropdowns = document.querySelectorAll(dropdownSelector)
    this._listDropdownsOptions = document.querySelectorAll(dropdownOptionsWrapperSelector)

    this._init()
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –≤—ã–ø–æ–¥–∞—é—â–µ–≥–æ —Å–ø–∏—Å–∫–∞
   * @return {void}
   */
  _init() {

    this.hideAllDropdowns()

    this._toggleHandler()

  }

  /**
   * –°–∫—Ä—ã–≤–∞–µ—Ç –≤—Å–µ –æ—Ç–∫—Ä—ã—Ç—ã–µ –≤—ã–ø–æ–¥–∞—é—â–∏–µ —Å–ø–∏—Å–∫–∏
   * @return {void}
   */
  hideAllDropdowns() {
    this._listDropdownsOptions.forEach(dropdownOpen => {

      if(this._target) {
        if(this._target.nextElementSibling !== dropdownOpen) {
          dropdownOpen.style.display = 'none';
        }
      }else {
        dropdownOpen.style.display = 'none';
      }

    })
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –∫–ª–∏–∫–∞ –ø–æ —ç–ª–µ–º–µ–Ω—Ç—É –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –≤—ã–ø–æ–¥–∞—é—â–∏–µ –º–µ–Ω—é
   * @return {void}
   */
  _toggleHandler() {
    this._listDropdowns.forEach(dropdown => {

      dropdown.addEventListener('click', (e) => {

        if (e.target && e.target.classList.contains(this._dropdownToggleSelector.slice(1))) {
          e.preventDefault()

          this._target = e.target;

          this._toggleOptions()
        }
      })

    })
  }

  /**
   * –û—Ç–∫—Ä—ã—Ç—å –≤—ã–ø–æ–¥–∞—é—â–∏–µ –º–µ–Ω—é
   * @return {void}
   */
  _toggleOptions() {
    this.hideAllDropdowns()

    this._target.nextElementSibling.style.display = this._target.nextElementSibling.style.display === 'none' ? 'block' : 'none'
  }
}
